export default {
	data() {
		return {
			info:''
		}
	},
	methods: {
		input(e){
			 this.info = e.detail.value;
			 console.log(this.info.length)
		},
		comment(){
			if(this.info.trim().length<=5){
				uni.showToast({
					title:"请填多于5个字",
					icon:"none"
				})
			}else{
				uniCloud.callFunction({
				    name: 'comment',
				    data: { 
						"info":this.info
					}
				  })
				  .then(res => {
					  uni.showToast({
					  	title:"感谢反馈",
						icon:"success"
					  })
				  });
			}				
		}
	}
}
